"use strict";

import Namespace from './Namespace';
import JSObjectNamespace from './JSObjectNamespace';

import {RookMethodFailed} from '../../exceptions';

export default class JSUtilsNamespace extends Namespace {

    constructor() {
        super();
    }

    callMethod(name, args) {
        switch (name) {
            case "exception":
            case "exception_string":
            case "exception_type":
                return new JSObjectNamespace("Indirect exception access is not supported on Node!");

            case "module":
                try {
                    return new JSObjectNamespace(require(args));
                } catch(e) {
                    throw new RookMethodFailed(name, args, e);
                }

            case "thread_id":
            case "thread_name":
            case "threads":
            case "thread_tracebacks":
                return new JSObjectNamespace("Threads are not supported on Node!");

            case "env":
                return new JSObjectNamespace(process.env[args]);

            default:
                super.callMethod(name, args);
        }
    }

}
